/** @format */

body {
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  color: #ffffff;
  background-color: #1f1f2c;
}

a {
  text-decoration: none;
  font-size: 14px;
  color: #ffffff;
  font-weight: 600;
}

h5 {
  font-size: 25px;
  font-weight: 600;
  color: #ffffff;
}

.button {
  border: 1px solid #ff5500;
  border-radius: 5px;
  background-color: #ff5500;
  width: 190px;
  height: 42px;
}

img {
  width: 100%;
}

.window__top {
  padding-top: 41px;
  display: flex;
}

.window__close {
  margin-left: 35px;
}

.top__logo {
  width: 98px;
  height: 30px;
  margin: 0 auto;
}

.description--title {
  margin-bottom: 42px;
}

.description--text {
  width: 360px;
  margin-bottom: 30px;
}

.description--text p {
  line-height: 1.5;
}

.owl-carousel {
  margin-top: 60px;
  margin-bottom: 20px;

  .first {
    background-image: url(../img/1.png);
    background-repeat: no-repeat;
    background-position: right;
    background-size: cover;
    width: 433px;
  }

  .item--description {
    padding-top: 80%;
    margin: 0 85px;
    width: 342px;
    padding-bottom: 13%;
  }

  .second {
    background-image: url(../img/second.png);
    background-repeat: no-repeat;
    background-position: right;
    background-size: cover;
    width: 433px;
  }

  .third {
    background-color: #2D2750;
    width: 475px;

    .item--description {
      padding-top: 0;
      width: 385px;
    }
  }

  .fourth {
    background-image: url(../img/four.png);
    background-repeat: no-repeat;
    background-position: right;
    background-size: cover;
    width: 433px;
  }

  .fifth {
    background-color: #7A5C0F;
    width: 410px;

    .item--description {
      padding-top: 70%;
      width: 320px;
    }
  }
}

.owl-item>div {
  cursor: pointer;
  margin: 8% 6%;
  transition: margin 0.2s ease;
}

.owl-item.center>div {
  cursor: auto;
  margin: 0 auto;
}

.owl-item:not(.center)>div:hover {
  opacity: 0.2;
}

.owl-carousel .owl-nav button.owl-next {
  position: absolute;
  top: 297px;
  right: 2px;
  width: 75px;
  height: 75px;
  background-color: #000000;
  opacity: 0.4;
  border-radius: 56px;
}

.owl-carousel .owl-nav button.owl-prev {
  position: absolute;
  top: 297px;
  left: 2px;
  width: 70px;
  height: 70px;
  background-color: #000000;
  opacity: 0.4;
  border-radius: 50px;
}

.owl-carousel .owl-nav button.owl-next span,
.owl-carousel .owl-nav button.owl-prev span {
  font-size: 60px;
}

.owl-carousel button.owl-dot {
  position: absolute;
  width: 40px;
  height: 3px;
  background-color: #C4C4C4;
  bottom: -87px;
  left: 404px;
  display: inline-block;
}

.owl-carousel button.owl-dot:hover {
  background-color: #fff;
}

.reaction {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  align-items: center;
}

.reaction .like,
.dislike {
  background-color: #000;
  border-radius: 56px;
  width: 20px;
  height: 20px;
  padding: 15px 15px;
  opacity: 0.5;
}

.reaction .dislike {
  padding-right: 0;
  padding: 15px 14px;
  margin-left: 30px;

}

.reaction .like a,
.dislike a {
  font-size: 15px;
  margin: 3px;

}

.reaction .like a:hover,
.dislike a:hover {
  color: #FF5500;
}